import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import '../CSS/Bingo.css'; // 

const Home = () => {
  const [inputValue, setInputValue] = useState('');
  const [languageMenuOpen, setLanguageMenuOpen] = useState(false); // 
  const navigate = useNavigate();
  const { t, i18n } = useTranslation();

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };

  const handleOpen = () => {
    if (inputValue) {
      navigate(`/open/${inputValue}`);
    } else {
      alert(t(''));
    }
  };

  const handleCreate = () => {
    navigate('/create');
  };

  const toggleLanguageMenu = () => {
    setLanguageMenuOpen(!languageMenuOpen);
  };

  const handleLanguageChange = (lng) => {
    i18n.changeLanguage(lng);
    setLanguageMenuOpen(false); // 
  };

  const currentLanguage = i18n.language === 'zh' ? '' : 'English';

  return (
    <div className="App">
      {/*  */}
      <div className="language-switcher">
        <button onClick={toggleLanguageMenu} className="language-button">
          {currentLanguage} 
        </button>
        {languageMenuOpen && (
          <ul className="language-menu">
            <li onClick={() => handleLanguageChange('en')}>English</li>
            <li onClick={() => handleLanguageChange('zh')}></li>
          </ul>
        )}
      </div>

      <img src="logo.png" alt="logo" style={{ width: '30em', height: 'auto' }} draggable="false" />
      <h1>{t('')}</h1>
      <div>
        <input
          type="text"
          value={inputValue}
          onChange={handleInputChange}
          placeholder={t('')}
          style={{ margin: '1em' }}
        />
        <button onClick={handleOpen}>{t('')}</button>
      </div>
      <div>
        <button onClick={handleCreate}>{t('')}</button>
      </div>
    </div>
  );
};

export default Home;
